  .nav--closed .nav__toggle:hover::before {
    background-color: black;
    box-shadow: 0 8px 0 0 black, 0 16px 0 0 black; }
  .nav--closed .nav__toggle:active::before {
    background-color: black;
    box-shadow: 0 8px 0 0 black, 0 16px 0 0 black; }
  .nav--opened .nav__toggle::before, .nav--opened .nav__toggle::after {
    content: "";
