    top: 20px;
    left: 13px;
    width: 27px;
    height: 2px;
    background-color: #afafaf; }
  .nav--opened .nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .nav--opened .nav__toggle::after {
